#detailform{
    margin-top: 5vh;
    width: 50vw;
    background-color: #363b46;
    border-radius: 20px;
    padding: 10px;
    line-height: calc(20px + 1vh);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
#detailform img{
    margin: 10px auto;
    width: 40vw;
    height: 40vw;
    border-radius: 20px;
}
#detailform span {
    background-color: #282C34;
    border-radius: 20px;
    padding: 2%;
}


@media (max-width: 650px) {
    #detailform{
        flex-direction: column;
    }
}

#detailform h3{
    text-align: center;
}
#detailform label{
    text-align: left;
    padding-inline-start: 20px;
}
#detailform input{
    width: 46vw;
    margin-left: -5px;
    background-color: white;
}
#detailform > label > textarea{
    width: 46vw;
    align-self: first baseline;
    vertical-align: top;
    margin-left: -5px;
    width: 46vw;
    height: 10vh;
    overflow-x: auto;
    border-radius: 10px;
}
#detailform button{
    background-color: black;
    color: white;
    border-radius: 20px;
    display: inline-block;
}
#detailform a button{
    width: 50vw;
}
#prevItem{ /*for AddInventory.js*/
    color: white;
    position: fixed;
    top: 70vh;
    left: 5vh;
}

#nextItem{ /*for AddInventory.js*/
    color: white;
    position: fixed;
    top: 70vh;
    right: 5vh;
}
